const rating = process.client ? require('vue-rate-it') : {}
export default {
  middleware: 'auth',
  components: {
    StarRating: rating.StarRating
  },
  async asyncData ({ $axios, route }) {
    try {
      let response = await $axios.$get(`${process.env.DEV_BACKEND}/api/products/${route.params.review}`)

      return {
        product: response.product
      }
    } catch (error) {
      console.log(error);
    }
  },

  data () {
    return {
      rating: 0,
      body: '',
      headline: '',
      selectedFile: null,
      fileName: null
    }
  },

  methods: {
    onSelectedFile (event) {
            this.selectedFile = event.target.files[0]
            this.fileName = event.target.files[0].name
    },

    async onAddReview () {
      try {
        // console.log('params: \t', this.$route.params);
        let data = new FormData()
        data.append('headline', this.headline)
        data.append('body', this.body)
        data.append('rating', this.rating)
        data.append('photo', this.selectedFile, this.selectedFile.name)

        let response = await this.$axios.$post(`${process.env.DEV_BACKEND}/api/review/${this.$route.params.review}`, data)

        if (response.success) {
          this.$router.push({path: `/products/${this.$route.params.review}`})
        }
      } catch (error) {
        console.log(error);
      }
    }
  }
}
